.friend_list {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.item {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  margin-bottom: 10px;
  padding: 10px 20px;
}

.name {
  font-size: 20px;
  font-weight: bold;
}

.avatar {
  width: 50px;
  margin-right: 10px;
}

.status {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.true {
  background-color: green;
}
.false {
  background-color: red;
}
